* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Karla', sans-serif;
  overflow: hidden;
}


/* navbar styling*/
/* for light mode */
.navbar {
  display: flex;
  justify-content: space-between;
  position: sticky;
  width: 100%;
  top: 0;
  left: 0;
  padding: 12px 30px;
  background-color: #5f8fe7;
  font-size: 20px;
  -webkit-transition: background-color 600ms linear;
  -ms-transition: background-color 600ms linear;
  transition: background-color 600ms linear;
}

/* for dark mode */
.navbarDark {
  display: flex;
  justify-content: space-between;
  position: sticky;
  width: 100%;
  top: 0;
  left: 0;
  padding: 12px 30px;
  background-color: #0c2c84;
  font-size: 20px;
  color: white;
  -webkit-transition: background-color 600ms linear;
  -ms-transition: background-color 600ms linear;
  transition: background-color 600ms linear;
}

/* for the toggle button of dark and light mode */
.toggler {
  display: flex;
  align-items: center;
  cursor: pointer;
}

.toggler p {

  font-size: 13px;
  font-weight: 600;
}

.togglerSlider {
  width: 25px;
  height: 13px;
  background-color: #0c2c84;
  cursor: pointer;
  margin-inline: 7px;
  display: flex;
  border-radius: 20px;
  justify-content: flex-start;
  border: 1px solid #0c2c84;
  align-items: center;
  box-sizing: content-box;
}

.togglerSliderDark {
  margin-inline: 7px;
  background-color: #5f8fe7;
  border-radius: 20px;
  cursor: pointer;
  width: 25px;
  border: 1px solid #5f8fe7;
  height: 13px;

}

.togglerSliderCircle {
  height: 13px;
  border-radius: 50%;
  width: 13px;
  background-color: white;
}

.togglerSliderCircleDark {
  height: 13px;
  border-radius: 50%;
  width: 13px;
  margin-left: auto;
  background-color: white;
}

.togglerLight {
  color: #2B283A;
}

.togglerDark {
  color: #D5D4D8;
}


.homePage {
  padding: 20px 30px;
  background-color: #0c2c84;
  height: 100vh;
  color: #ffffff;
  -webkit-transition: background-color 600ms linear;
  -ms-transition: background-color 600ms linear;
  transition: background-color 600ms linear;
}

.homePageWhite {
  padding: 20px 30px;
  background-color: #5f8fe7;
  color: black;
  height: 100vh;
  -webkit-transition: background-color 600ms linear;
  -ms-transition: background-color 600ms linear;
  transition: background-color 600ms linear;
}

.question {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: start;
  color: white;
  height: 100%;
  padding: 20px 0px;
  text-align: center;
  animation: question-load 300ms ease-in 300ms;
  animation-fill-mode: forwards;
  transform: translateY(-20px);
  opacity: 0;
}

.questionWhite {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: start;
  height: 100%;
  padding: 20px 0px;
  text-align: center;
  color: black;
  animation: question-load 300ms ease-in 300ms;
  animation-fill-mode: forwards;
  transform: translateY(-20px);
  opacity: 0;
}

.optionInput {
  visibility: hidden;
}

.label {
  height: 30px;
  font-family: 'Karla';
  font-size: 18px;
  cursor: pointer;
  background-color: transparent;
  border: none;
  border-radius: 12px;
  animation: question-load 300ms ease-in 600ms;
  animation-fill-mode: forwards;
  transform: translateY(-20px);
  opacity: 0;
}

.label:hover {
  cursor: pointer;
  background-color: #fffc00;
  border-radius: 2px;
  width: 500px;
  color: #0c2c84;
}

.customOption {
  left: -8px;
  top: 6px;
  cursor: pointer;
  width: 19px;
  height: 19px;
  border: 2px solid #fffc00;
  border-radius: 50%;
  display: inline-block;
  position: relative;
  animation: question-load 300ms ease-in 600ms;
  animation-fill-mode: forwards;
  transform: translateY(-20px);
  opacity: 0;
}

.customOptionBorder {
  border: 2px solid red;
  ;
}

.customOption::after {
  content: '';
  width: 12px;
  height: 12px;
  background: #ff0000;
  position: absolute;
  border-radius: 50%;
  top: 50%;
  left: 51%;
  transform: translate(-50%, -50%);
  opacity: 0;
  transition: opacity 0.2s;
}

.optionInput:checked+span {
  border: 2px solid #ff0000;
}

.optionInput:checked+span:after {
  opacity: 1;
}

/* submit button */
.homePageButton {
  margin-top: 29px;
  width: 100px;
  padding: 10px 1px;
  border-radius: 15px;
  border: none;
  background-color: #fffc00;
  color: #0c2c84;
  font-weight: 600;
  font-family: 'Karla', sans-serif;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
  animation: question-load 300ms ease-in 900ms;
  animation-fill-mode: forwards;
  transform: translateY(-20px);
  opacity: 0;
}

.homePageButton:hover {
  background-color: #212121;
  color: #fffc00;
  cursor: pointer;
}


/* page 2 */
.page2 {
  display: flex;
  height: 100vh;
  width: 100%;
  background-color: #0c2c84;
  justify-content: center;
  align-items: center;
  text-align: center;
  animation: question-load 300ms ease-in 300ms;
  animation-fill-mode: forwards;
  transform: translateY(-20px);
  opacity: 0;
  -webkit-transition: background-color 600ms linear;
  -ms-transition: background-color 600ms linear;
  transition: background-color 600ms linear;
} 

.page2White {
  display: flex;
  height: 100vh;
  width: 100%;
  background-color: #5f8fe7;
  justify-content: center;
  align-items: center;
  text-align: center;
  animation: question-load 300ms ease-in 300ms;
  animation-fill-mode: forwards;
  transform: translateY(-20px);
  opacity: 0;
  -webkit-transition: background-color 600ms linear;
  -ms-transition: background-color 600ms linear;
  transition: background-color 600ms linear;
}

table {
  text-align: center;
  border: 2px solid forestgreen;
  width: 1000px;
  height: 470px;
  background-color: white;
}



th {
  border: 1px solid black;
  padding: 3px 5px;
}

th:nth-child(1) {
  background-color: rgb(222, 170, 74);
  color: rgb(95, 62, 2);
}

td {
  text-align: center;
  border: 1px solid black;
  padding: 11px 14px;

}



td:nth-child(1) {
  background-color: pink;
}

td:nth-child(2) {
  background-color: yellow;
}

td:nth-child(3) {
  background-color: lightgreen;
}

td:nth-child(4) {
  background-color: rgb(204, 150, 255);
}

td:nth-child(5) {
  background-color: lightblue;
}

@media (max-width: 768px) {
  body {
    overflow: auto;
  }

  table {
    width: 100%;
    overflow-x: auto;
  }

  .page2 {
    overflow-x: auto;
    justify-content: none;
    align-items: none;
    text-align: none;
  }

  .page2White {
    overflow-x: auto;
    justify-content: none;
    align-items: none;
    text-align: none;
  }
}
@keyframes nav-load {
  0% {
    transform: translateX(-3%);
    opacity: 0;
  }

  100% {
    transform: translateX(0);
    opacity: 1;
  }
}
@keyframes question-load {
  0%{
    transform: translateY(-20px);
    opacity: 0;
  }

  100%{
    transform: translateY(0px);
    opacity: 1;
  }
}